//получаем кнопки 
const tabsBtn = document.querySelectorAll('.tabs__btn');
//получаем табы
const tabsItems = document.querySelectorAll('.tabs__item');

//объвяляем функцию с метода форич 
tabsBtn.forEach(function(item) {
    item.addEventListener("click", function() {

        let currentBtn = item;//переменная самой кнопки
        let tabId = currentBtn.getAttribute("data-tab"); //получаем атрибуты в переменную
        let currentTab = document.querySelector(tabId); //обращаемся к переменной


        if (! currentBtn.classList.contains('active') ) {

       
        //функция удаления класса активе у кнопок
        tabsBtn.forEach (function(item) {
            item.classList.remove('active');
        });

        //добавляем класс актив
        currentBtn.classList.add('active');
      

        //объявляем функцию айтем с помощью метода forEach для удаления классов 
        tabsItems.forEach(function(item){
            item.classList.remove('active');
        });
        //добавляем класс актив
        currentTab.classList.add('active');
        }
    });
});